.work {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin-top: 10px;
}

.workInfo {
    padding: 25px 20px;
    width: 100%;
    max-width: 336px;
}

.workTitle {
    font-family: 'Josefin Sans', sans-serif;
    font-size: 16px;
    letter-spacing: 1px;
    margin: 0 0 5px;
    line-height: 1.2;
    color: #fff;
}

.workDescription {
    font-family: 'Poppins', sans-serif;
    line-height: 1.8;
    font-weight: 400;
    font-size: 12px;
    color: #fff;
}

.photo {
    border-radius: 10px;
    width: 300px;
    height: 350px;
    background-color: #f7f7f7;
    object-fit: cover;
    position: relative;
    background-size: cover;
}

.imageWrapper {
    position: relative;
    transition: transform .3s ease-in-out;
    overflow: hidden;
    border-radius: 10px;
}

.imageWrapper:hover img {
    transform: scale(1.1);
}

.background {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    background: #161625d6;
    opacity: 0.4;
    width: 100%;
    height: 99%;
    transition: all .3s ease-in-out;
}

.background:hover {
    opacity: 0;
    transform: scale(1.1);
}

.workBtn {
    text-align: end;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: fit-content;
    align-self: center;
    /*background: #161625d6;*/
    background: #5ac24e;
    /*border: 1px solid grey;*/
    border-radius: 4px;
    color: #fff;
    cursor: pointer;
    font-size: 20px;
    /*font-weight: 700;*/
    font-weight: bold;
    letter-spacing: 1px;
    padding: 10px 20px;
    font-family: 'Josefin Sans', sans-serif;
    /*text-transform: uppercase;*/
    z-index: 1;
}

@media screen and (max-width: 768px) {
    .photo {
        width: 100%;
        height: 200px;
    }

}